import styled from 'styled-components'

export const Image = styled.img`
width: 100%;
max-width: 720px;
transition: all 0.3s;
object-fit: cover;
border-radius: 20px;
animation: anime 0.4s;

:hover{
    opacity: 0.7;
    cursor: pointer;
}

@keyframes anime {
from{
    opacity: 0;
}
to{
    opacity: 1;
}
}
`;