import React from 'react'
//styles
import {Wrapper, Content , Text} from './HeroImage.styles'
 
const HeroImage = ({imgae , title , text})=>(
    <Wrapper imgae={imgae}>
     <Content>
         <Text>
             <h1>{title}</h1>
             <p>{text}</p>
         </Text>
     </Content>

    </Wrapper>
)

export default HeroImage